import './navbar.css';
import logo from './logo-home.png'

function Log_Out() {
    sessionStorage.clear();
    window.location.reload();
}

function Navbar() {

    var lang = localStorage.getItem('lang')
    if (lang === null) {
        localStorage.setItem('lang', 'en-us')
        lang = localStorage.getItem('lang')
    }

    let button;
    if (sessionStorage.getItem('userAuthenticated')) {
        if (sessionStorage.getItem('userAuthenticated').match('true')) {
            button = <a className="btn btn-outline-danger navbarText nav-link dropdown-toggle" id="navbarDropdownMenuLink"
                        data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">{sessionStorage.getItem('firstName')} </a>
        } else {
            button = <a className="btn btn-outline-danger navbarText" href="/login" role="button">Log In</a>
        }
    }
    else {
        button = <a className="btn btn-outline-danger navbarText" href="/login" role="button">Log In</a>
    }

    return (
        <nav id="nav" className="navbar navbar-expand-lg navbar-dark bg-secondary">
            <a className="navbar-brand" href="/">
                <div className="logo">
                    <img src={logo} alt="logo"></img>
                </div>
            </a>
            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarText"
                    aria-controls="navbarText" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
            </button>
            <div className="collapse navbar-collapse" id="navbarText">
                <ul className="navbar-nav mr-auto">
                    <li className="nav-item active">
                        <a className="nav-link" href="/">Home</a>
                    </li>
                    <li className="nav-item active">
                        <a className="nav-link" href="#/about">About</a>
                    </li>
                </ul>
                <div className="nav-item dropdown button-dp">
                    {button}
                    <div className="dropdown-menu dropdown-menu-right" aria-labelledby="navbarDropdownMenuLink">
                        <a className="dropdown-item" href="#/profile">Profile</a>
                        <a className="dropdown-item" href="" data-toggle="modal" data-target="#exampleModal">Log out</a>
                    </div>
                </div>
            </div>
            <div className="modal fade " id="exampleModal" tabIndex="-1" aria-labelledby="exampleModalLabel"
                 aria-hidden="true">
                <div className="modal-dialog">
                    <div className="modal-content">
                        <div className="modal-header">
                            <h5 className="modal-title" id="exampleModalLabel">Warning</h5>
                            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="modal-body">
                            Are you sure that you want to log out ?
                        </div>
                        <div className="modal-footer">
                            <button type="button" className="btn btn-dark" data-dismiss="modal">Cancel</button>
                            <button onClick={e => Log_Out()} className="btn btn-dark bg-danger border-danger">Logging out</button>
                        </div>
                    </div>
                </div>
            </div>
        </nav>
    );
}

export default Navbar;
