import React, { useState } from 'react';
import { Redirect } from 'react-router-dom';
import { nameServer, apiKey } from "../Conf/config";
import './login.css';

async function webLogin(email, password) {
    const requestOptions = {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json'
        },
        body: JSON.stringify({
            function: 'login',
            email: email,
            password: password
        })
    };
    try {
        const response = await fetch(nameServer + '/mspr', requestOptions);
        const data = await response.json();
        return data;
    } catch (e) {
        const res = new Response(JSON.stringify({
            statusCode: 201,
            data: 'Oops, something went wrong with your system'
        }));
        const resJson = await res.json();
        return resJson;
    }
}

function Authentication(emailAuth, passwordAuth) {
    if (emailAuth !== "" && passwordAuth !== "") {
        webLogin(emailAuth, passwordAuth)
            .then(function (res) {
                if (res.statusCode === 200) {
                    sessionStorage.setItem('userAuthenticated', 'true');
                    sessionStorage.setItem('email', res.data.user.email);
                    sessionStorage.setItem('firstName', res.data.user.firstName);
                    sessionStorage.setItem('lastName', res.data.user.lastName);
                    sessionStorage.setItem('department', res.data.user.department);
                    sessionStorage.setItem('privilege', res.data.user.privilege);
                    window.location.reload();
                } else {
                    alert(res.data);
                }
            });
        console.log('Connecting...');
    } else {
        alert('Some fields were left empty');
    }
}

function Login() {
    const [buttonValue, setButtonValue] = useState("Submit");
    const [buttonColor, setButtonColor] = useState("bg-danger border-danger");
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");

    if (sessionStorage.getItem('userAuthenticated')) {
        if (sessionStorage.getItem('userAuthenticated').match('true')) {
            return <Redirect to="/profile"/>;
        }
    }

    return (
        <div>
            <div className="accordion" id="accordionExample">
                <div className="card">
                    <div className="card-header bg-secondary" id="headingOne">
                        <h2 className="mb-0">
                            <button className="btn btn-dark bg-danger text-left border-danger" type="button" data-toggle="collapse"
                                    data-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
                                Authenticate
                            </button>
                        </h2>
                    </div>
                    <div id="collapseOne" className="collapse show" aria-labelledby="headingOne"
                         data-parent="#accordionExample">
                        <div className="card-body">
                            <form>
                                <div className="form-group">
                                    <label htmlFor="exampleInputEmail1">Email address</label>
                                    <input type="email" className="form-control" id="exampleInputEmail1"
                                           aria-describedby="emailHelp" value={email}
                                           onChange={e => setEmail(e.target.value)} required />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="exampleInputPassword1">Password</label>
                                    <input type="password" className="form-control" id="exampleInputPassword1" value={password}
                                           onChange={e => setPassword(e.target.value)} required />
                                </div>
                            </form>
                            <div className="col-md-4 col-lg-2">
                                <button onClick={() => {
                                    setButtonValue('Hold on...');
                                    setButtonColor('bg-light border-light');
                                    Authentication(email, password)
                                    }} className={`btn btn-dark ${buttonColor} btn-block`}>{buttonValue}</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Login;
