import { Redirect } from 'react-router-dom';
import './profile.css';

function Profile() {
    if (sessionStorage.getItem('userAuthenticated')) {
        if (!sessionStorage.getItem('userAuthenticated').match('true')) {
            return <Redirect to="/login"/>;
        }
    }
    else {
        return <Redirect to="/login"/>;
    }
    return (
        <div className="Profile">
            <p>Votre mail est : { sessionStorage.getItem('email') }</p>
            <p>Identitée: { sessionStorage.getItem('firstName') } { sessionStorage.getItem('lastName') }</p>
            <p>Department : { sessionStorage.getItem('department') }</p>
            <p>Privilege level : { sessionStorage.getItem('privilege') }</p>
        </div>
    );
}

export default Profile;
